.projects-container {
  padding: 30px 10%;
  background: #333333;
  text-align: justify;
  color: white;
}

.project:not(:last-child) {
  border-bottom: 1px solid white;
}
.project {
  padding: 20px 0;
  padding-top: 50px;
}
.project-content {
  display: flex;
  justify-content: space-around;
}

.carousel.slide {
  padding: 0 30px;
}
.picture-container {
  padding: 0 30px;
  width: 50%;
}

.project-footer {
  display: flex;
  flex-wrap: wrap;
}

.badge,
.project-footer .btn {
  margin: auto 5px auto 0;
}

.stack-tag,
.link-btn {
  padding-bottom: 10px;
  width: 100%;
}

.text-area-right {
  width: 50%;
  line-height: 30px;
}

.carousel,
.picture-container {
  margin-top: 10px;
}
