.resume-container {
  background: #555555;
  display: flex;
  padding: 4%;
  color: white;
  justify-content: space-around;
}

.experience {
  padding: 0 20px;
  max-width: 60%;
  text-align: left;
}
.experience ul {
  padding: 0 20px;
  list-style: square;
}
.skill {
  padding: 0 20px;
  max-width: 50%;
}
.skill-content:not(:last-child) {
  margin: auto;
  border-bottom: 1px solid;
}

.figure-caption {
  color: white;
}
.work-header {
  display: flex;
  justify-content: space-between;
}
.skill-content img {
  max-width: 50px;
  max-height: 50px;
  margin: 0 10px;
}

.skill-content h4 {
  padding: 20px;
}

.experience h4 {
  padding: 10px 0;
}
