.footer-container {
  height: 150px;
  padding-top: 50px;
  position: relative;
  background: white;
}

.footer-container i {
  font-size: 30px;
  padding: 20px;
  color: black;
}

.footer-container i:hover {
  color: gray;
}

.footer-container .nav-up {
  position: absolute;
  padding: 15px;
  font-size: 40px;
  color: white;
  top: -35px;
  left: calc(50% - 30px);
  background-color: #333333;
  border-radius: 100%;
  transform: scaleY(0.9);
  border: 0;
}

.footer-container .nav-up:hover {
  color: #11abb0;
}
