.home-content {
  position: relative;
  top: 20%;
  color: white;
}
.home-content h1 {
  padding: 30px;
  font-size: 60px;
}
.home-content h3 {
  width: 60%;
  margin: auto;
}
.home-contact {
  height: 150px;
}

.home-contact i {
  font-size: 30px;
  padding: 0 20px;
  color: white;
}

.home-contact i:hover {
  color: gray;
}

.division-line {
  border-top: 1px solid gray;
  width: 30%;
  margin: 50px auto;
}

.nav-down {
  padding: 15px;
  font-size: 40px;
  color: black;
  background-color: white;
  border-radius: 100%;
  transform: scaleY(0.9);
  border: 0;
}
.nav-down:hover {
  background-color: #11abb0;
}
