.header-item {
  font-weight: bold;
  color: white;
  background: transparent;
  padding: 5px 10px;
}

.header-item:hover {
  text-decoration: none;
  color: #11abb0 !important;
  background: transparent !important;
}
.header-item:focus {
  color: orange;
  background: transparent !important;
}
.header-bar {
  display: flex;
  justify-content: center;
  background-color: transparent;
  position: fixed;
  width: 100%;
  z-index: 100;
  transition: width 2s, height 2s, background-color 1s, transform 1s;
  transition-delay: 0.3s;
}

.header-bar:hover {
  background-color: #222222 !important;
}

.project-dropdown {
  text-align: left;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  position: fixed;
  background: rgb(34, 34, 34);
  margin-top: 12px;
  /* padding-bottom: 5px; */
  transition: width 0.1s, height 0.3s, opacity 1s, background-color 1s,
    transform 1s;
  transition-delay: 0.3s;
  overflow: auto;
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}

.project-dropdown::-webkit-scrollbar-thumb,
.project-dropdown::-webkit-scrollbar-track {
  display: none;
}
.project-dropdown:hover::-webkit-scrollbar-thumb,
.project-dropdown:hover::-webkit-scrollbar-track {
  display: initial;
}
