.contact-container {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  /* background: #555555; */
  padding: 120px 0;
  color: white;
}
.contact-container h2 {
  margin: 40px;
}
.contact-content {
  margin: auto;
  width: fit-content;
}
.contact-list {
  list-style: none;
  text-align: left;
  padding: 20px 5px;
}
.contact-list li {
  padding: 10px 0;
}
.contact-list a {
  text-decoration: none;
  color: #11abb0;
}

.contact-list a:hover {
  color: white;
}
